import React from 'react'
import Button from './Button'

const SearchBar = (props) => {
  return (
    <div>
      <form>
        <input 
          type="text"
          onChange={props.onChange}
          value={props.value}
          placeholder="Search Text Here"
        />
        <Button onClick={props.onClick} name="SEARCH"/>
      </form>
    </div>
  )
}

export default SearchBar