import React, {Component} from 'react'
import Header from './Header'
import recipeData from '../data/RecipeData'
import RecipeList from './RecipeList'
import SearchBar from './SearchBar'
import RecipeCard from './RecipeCard'
import '../index.css'

class App extends Component {
  constructor() {
    super()
    
    this.state = {
      inputSearch: '',
      recipes: [],
      selectedRecipe: {}
    }
  }

  renderRecipe = (event) => {
    const {id} = event.target
    const {recipes} = this.state
      this.setState({
        selectedRecipe: recipes[id]
      })
  }

  handleClick = (e) => {
    e.preventDefault()
    const {inputSearch} = this.state
    const filteredList = recipeData.filter((recipe, i) => recipe.name.toLowerCase().indexOf(inputSearch.toLowerCase()) > -1)
    this.setState({
      recipes: filteredList
    })
    console.log(filteredList)
  }

  handleChange = (e) => {
    const {value} = e.target
    this.setState({
      inputSearch: value
    })
  }

  render() {
    const {inputSearch, selectedRecipe, recipes} = this.state
    const ingredients = !selectedRecipe.ingredients ? null : selectedRecipe.ingredients.map((item, i) => <li key={i}>{item}</li>)
    const instructions = !selectedRecipe.instructions ? null : selectedRecipe.instructions.map((item, i) => <li key={i}>{item}</li>)
    return (
      <div className="app">
        <Header />
        <section className="left-section">
          <SearchBar 
            onChange={this.handleChange}
            value={inputSearch}
            onClick={this.handleClick}
          />
          <RecipeList 
            term={inputSearch}
            filteredList={recipes}
            onClick={this.renderRecipe}
          />
        </section>
        <section className="right-section">
          <RecipeCard 
            name={selectedRecipe.name}
            ingredients={ingredients}
            instructions={instructions}
            ingredientsHeader={!selectedRecipe.ingredients ? null : 'INGREDIENTS'}
            instructionsHeader={!selectedRecipe.instructions ? null : 'INSTRUCTIONS'}
          />
        </section>  
      </div>
    )
  }
}

export default App